.contacts {

  &__top {
    margin-bottom: 32px;
  }

  &__map {
    border-radius: 24px;
    overflow: hidden;
    max-height: 350px;

    @include media-min ($lg) {
      max-height: 410px;
    }

    @include media-min ($xxl) {
      max-height: 500px;
    }
  }

  &__scroll-block {
    overflow-y: auto;
    direction: rtl;
    overscroll-behavior: none;

    @include scrollbar(var(--gray-30-color), var(--blue-color), 2px, 4px);
  }

  &__list {
    position: relative;
    display: grid;
    row-gap: 24px;
    direction: ltr;

    @include media-min ($lg) {
      padding-left: 16px;
      row-gap: 32px;
      max-height: 500px;
    }

    @include media-min ($xxl) {
      max-height: 600px;
    }
  }
}

.contacts-manager {
  margin-top: 32px;
  padding-top: 32px;

  @include media-min ($lg) {
    margin-top: 48px;
    padding-top: 48px;
  }

  &__title {
    margin-bottom: 48px;

    @include media-min ($xxl) {
      margin-bottom: 64px;
    }
  }

  &__list {
    display: grid;
    row-gap: 64px;

    @include media-min ($lg) {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 30px;
    }

    & > li {
      &::before {
        content: '';
        position: absolute;
        top: -32px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: var(--gray-30-color);
      }

      @include media-min ($lg) {
        &::before {
          top: 0;
          left: -15px;
          width: 1px;
          height: 100%;
          border-left: 1px solid var(--gray-30-color);
        }
      }
    }
  }
}

.contacts-department {
  padding: 64px 0;

  @include media-min ($xl) {
    padding-bottom: 96px;
  }

  @include media-min ($xxl) {
    padding-top: 96px;
  }

  &__top {
    padding-left: max(15px, calc((100% - var(--content-width, 540px)) / 2));

    @include media-min ($md) {
      --content-width: 690px;

      padding-left: 0;
      width: min(var(--content-width), 100%);
      margin: 0 auto;
    }

    @include media-min ($lg) {
      --content-width: 930px;
    }

    @include media-min ($xl) {
      --content-width: 1110px;
    }

    @include media-min ($xxl) {
      --content-width: 1430px;
    }

    @include media-min ($fhd) {
      --content-width: 1590px;
    }
  }

  &__btns {
    display: flex;
    margin-bottom: 64px;
    overflow-y: hidden;
    padding-right: 15px;

    @include media-min ($lg) {
      overflow: visible;
      padding-right: 0;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 30px;
    }

    @include media-min ($xl) {
      margin-bottom: 96px;
    }

    & > li {
      position: relative;
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }

      &:first-child {
        &::before {
          content: none;
        }
      }

      @include media-min ($lg) {
        margin-right: 0;
      }

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -15px;
        width: 1px;
        height: 100%;
        background-color: var(--gray-30-color);
      }

      &:nth-child(3n+1) {
        @include media-min ($lg) {
          &::before {
            content: none;
          }
        }
      }
    }
  }

  &__btn {
    --main-title-lg-color: var(--gray-30-color);

    text-align: start;
    transition: color var(--transition-speed-medium) ease-in-out;
    text-decoration: underline;
    min-width: 200px;

    @include media-min ($xl) {
      font-size: 32px;
    }

    @include media-min ($xxl) {
      padding-right: 70px;
    }

    &.is-selected {
      --main-title-lg-color: var(--blue-color);

      text-decoration: none;
    }
  }

  &__list {
    @include media-min ($lg) {
      grid-template-columns: repeat(2, 1fr);
      row-gap: 96px;
    }

    @include media-min ($xxl) {
      grid-template-columns: repeat(3, 1fr);
    }

    & > li {

      &:first-child {
        &::before {
          content: none;
        }
      }

      @include media-min ($lg) {
        &:nth-child(2n+1) {
          &::before {
            content: none;
          }
        }
      }

      @include media-min ($xxl) {
        &:nth-child(2n+1) {
          &::before {
            content: '';
          }
        }

        &:nth-child(3n+1) {
          &::before {
            content: none;
          }
        }
      }
    }
  }

  & .manager-card {
    @include media-min ($xl) {
      padding: 0 64px 0 24px;
    }
  }
}

.office-addresses {
  padding: 16px 0 64px;

  @include media-min ($lg) {
    padding-top: 32px;
  }

  @include media-min ($xxl) {
    padding-bottom: 96px;
  }
}

.main-contacts {
  padding: 48px 0;

  @include media-min ($lg) {
    padding: 64px 0 96px;
  }

  @include media-min ($xxl) {
    padding: 96px 0;
  }

  &__list {

    & > li {
      @include media-min ($lg) {
        &:nth-child(3n+1) {
          &::before {
            content: none;
          }
        }
      }
    }
  }
}